.section-title {
  position: relative;
  text-align: center;
  padding: 75px 0 30px;
}
.section-title:after {
  background: url("../../assets/images/icons/broken-line.svg") no-repeat center;
  background-size: contain;
  transform: translateX(-50%);
  content: "";
  width: 100%;
  position: absolute;
  height: 60px;
  top: 15px;
  left: 50%;
}

.nav-star {
  margin-top: 7px;
}
.nav-star a.link {
  padding: 0 2px;
}
.nav-star a.link svg {
  width: 16px;
  fill: #fff;
  stroke: #00dcdc;
  transition: fill 0.3s ease;
}
.nav-star a.link:hover svg {
  fill: #00dcdc;
  stroke: none;
}
.nav-star a.link:hover ~ a svg {
  fill: #00dcdc;
  stroke: none;
}

.slide-btn {
  background: transparent;
  padding: 0.85em 0.75em;
  margin: 1rem;
  position: relative;
  z-index: 1;
  overflow: hidden;
  transition: color 1s ease;
}
.slide-btn::after {
  content: "";
  background: #fff;
  position: absolute;
  z-index: -1;
  display: block;
  transition: all 1s ease;
}
.slide-btn.slide_from_left::after {
  top: 0;
  bottom: 0;
  left: -100%;
  right: 100%;
}
.slide-btn.slide_from_right::after {
  top: 0;
  bottom: 0;
  left: 100%;
  right: -100%;
}
.slide-btn.slide_from_top::after {
  left: 0;
  right: 0;
  top: -100%;
  bottom: 100%;
}
.slide-btn.slide_from_bottom::after {
  left: 0;
  right: 0;
  top: 100%;
  bottom: -100%;
}
.slide-btn:hover::after {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.container {
  margin: 0 auto;
  width: 1024px;
}
.container-holder {
  padding: 0 15px;
}

*, *::before, *::after {
  box-sizing: border-box;
}
html {
  font-size: 14px;
  overflow-x: hidden;
}

body {
  margin: 0;
  font-family: "karla", "sans-serif";
}

a {
  text-decoration: none;
  color: #949494;
  display: block;
}
a:hover {
  color: #000;
}

p {
  margin: 0;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

i {
  font-style: normal;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  display: block;
  margin: 0 auto;
}

header#header {
  background-color: #fff;
}
header#header .header_topbar {
  border-bottom: solid 1px #ddd;
}
header#header .header_topbar ul {
  margin: 0;
  text-align: right;
}
header#header .header_topbar ul li {
  padding: 1.3rem 1.7rem;
  border-left: solid 1px #ddd;
  display: inline-block;
}
header#header .header_topbar ul li:last-child {
  padding-right: 5px;
}
header#header .header_topbar ul li a .tobbar_link-label {
  color: #949494;
}
header#header .header_topbar ul li a .tobbar_link-label:hover {
  color: #000;
}
header#header .header_bottombar {
  padding: 20px 30px;
  box-shadow: 0 5px 10px 1px rgba(0, 0, 0, 0.05);
}
header#header .header_bottombar-left-area, header#header .header_bottombar-right-area {
  width: 20%;
  margin-top: 15px;
}
header#header .header_bottombar-center-area {
  width: 60%;
  text-align: center;
}
header#header .header_bottombar-center-area ul li a {
  padding: 20px;
  font-weight: bold;
}
header#header .header_bottombar-center-area ul li a.active {
  color: #000;
}
header#header .header_bottombar-right-area {
  direction: rtl;
}
header#header .cart_number {
  border: solid 2px #000;
}
header#header .wishlist_number,
header#header .cart_number {
  background: #e8e8e8;
  color: #000;
  padding: 5px 15px;
  margin: 0 10px;
}

header#mobile-header {
  padding: 10px 0;
  display: none;
}
header#mobile-header .mobile-header__responsive {
  height: 40px;
}
header#mobile-header .mobile-header__responsive ul li {
  font-size: 1.2rem;
  display: inline-block;
}
header#mobile-header .mobile-header__responsive ul li a {
  padding: 0 5px;
}
header#mobile-header .mobile-header__responsive ul.icon-left-section, header#mobile-header .mobile-header__responsive ul.icon-right-section {
  margin-top: 10px;
}
header#mobile-header .mobile-header__responsive ul.icon-left-section li.basket span {
  position: absolute;
  right: -10px;
  top: -10px;
  background: #ff0000;
  color: #fff;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  -moz-border-radius: 50%;
  width: 17px;
  height: 17px;
  font-size: 10px;
  z-index: 1;
  line-height: 18px;
  text-align: center;
}
header#mobile-header .mobile-header__responsive ul.icon-right-section li:last-child, header#mobile-header .mobile-header__responsive ul.icon-left-section li:last-child {
  margin-left: 15px;
}
header#mobile-header .mobile-header__responsive .mobile-header__logo {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
header#mobile-header .mobile-header__responsive .mobile-header__logo img {
  max-width: 10rem;
  height: auto;
}

.gray-background:before {
  content: "";
  position: absolute;
  background-color: #e8e8e8;
  top: 0;
  height: 200px;
  left: 0;
  width: 100%;
  z-index: -1;
}

/*chair*/
#chair-section {
  padding-bottom: 40px;
}
#chair-section .chairParent .chair .title {
  font-size: 1.5rem;
  margin: 0;
}
#chair-section .chairParent .chair img {
  max-width: 250px;
  height: auto;
}
#chair-section .chairParent .chair .price, #chair-section .chairParent .chair .stars {
  display: block;
  margin-top: 7px;
  color: #949494;
}
#chair-section .chairParent .chair .stars {
  margin-top: 7px;
}
#chair-section .arrows i.icon {
  font-size: 1.4rem;
  color: #949494;
}
#chair-section .arrows i.icon:hover {
  color: #000;
}
#chair-section .arrows_left, #chair-section .arrows_right {
  top: 45%;
  transform: translateY(-45%);
  position: absolute;
  padding: 7px 14px;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.05);
  background-color: #fff;
  border-radius: 2px;
  cursor: pointer;
}
#chair-section .arrows_left {
  left: 10px;
}
#chair-section .arrows_right {
  right: 10px;
}

/*rooms*/
#rooms .rooms_category {
  width: 33.33%;
}
#rooms .rooms_category img {
  width: 100%;
  height: auto;
}
#rooms .rooms_category-name {
  letter-spacing: 3px;
  color: #fff;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 50%;
  z-index: 1;
}
#rooms .rooms_category-name h3 {
  margin-bottom: 2rem;
}
#rooms .rooms_category-name .shop-btn {
  padding: 10px 15px;
  color: #fff;
  border: solid 2px #fff;
  min-width: 180px;
  display: block;
}
#rooms .rooms_category-name .shop-btn:hover {
  font-weight: bold;
  color: #000;
}
#rooms .rooms_category:after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #000;
  opacity: 0.4;
  transition: opacity 0.3s ease;
}
#rooms .rooms_category:hover::after {
  opacity: 0.6;
}

/*features*/
#features .features_products {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
#features .features_products .features_product-item {
  display: flex;
  flex-direction: row;
  width: 33.33%;
  margin-top: 25px;
}
#features .features_products .features_product-item .product-thumbnail,
#features .features_products .features_product-item .product-description {
  width: 50%;
}
#features .features_products .features_product-item .product-description .product-holder {
  padding: 0 10px;
}
#features .features_products .features_product-item .product-description .product-holder .product-title {
  text-transform: capitalize;
}
#features .features_products .features_product-item .product-description .product-holder .product-price {
  margin: 10px 0;
}
#features .features_products .features_product-item .product-description .product-holder .product-price .product-off {
  text-decoration: line-through;
}
#features .features_products .features_product-item .product-thumbnail {
  transition: transform 0.6s ease;
}
#features .features_products .features_product-item .product-thumbnail:hover {
  transform: rotateY(180deg);
}
#features .features_products .features_product-item .product-thumbnail img {
  width: 100%;
  height: auto;
}

/*latest*/
#latest .latest_gallery-item {
  width: 25%;
  transition: transform 0.4s ease;
}
#latest .latest_gallery-item a {
  display: block;
}
#latest .latest_gallery-item a img {
  width: 100%;
  height: auto;
}
#latest .latest_gallery-item:hover {
  transform: scale(0.9, 0.9);
}

/*about us */
#aboutUs .about-delphinus-holder {
  margin: 0 15px;
}
#aboutUs .about-delphinus {
  width: 66.66%;
}
#aboutUs .about-delphinus-background {
  background: url("../../assets/images/delphinus/abou-us-background.jpg") center no-repeat;
  background-size: cover;
  line-height: 29px;
  color: #fff;
}
#aboutUs .about-delphinus-background .about-delphinus-spacing {
  padding: 30px;
}
#aboutUs .about-delphinus-background .about-delphinus-spacing h3 {
  letter-spacing: 5px;
}
#aboutUs .about-delphinus-background .about-delphinus-spacing .about-delphinus-description {
  margin-bottom: 30px;
}
#aboutUs .news-letter {
  width: 33.33%;
}
#aboutUs .news-letter-spacing {
  padding: 30px 30px 136px;
  border: solid 2px #ddd;
}
#aboutUs .news-letter-spacing p.news-letter-desc {
  margin: 15px 0;
}
#aboutUs .news-letter form input {
  padding: 10px 20px;
  border: solid 2px #ddd;
  width: 100%;
}
#aboutUs .news-letter form button[type=submit] {
  position: absolute;
  right: 13px;
  top: 50%;
  transform: translateY(-50%);
  color: #949494;
  background: none;
  border: none;
}
#aboutUs .aboutUs_rows-images-item {
  width: 33.33%;
}
#aboutUs .aboutUs_rows-images-item-holder {
  margin: 30px 15px;
  overflow: hidden;
}
#aboutUs .aboutUs_rows-images-item-holder img {
  width: 100%;
  height: auto;
  transition: transform 0.3s ease;
}
#aboutUs .aboutUs_rows-images-item-holder img:hover {
  transform: scale(1.1, 1.1);
}
#aboutUs .aboutUs_rows-brands {
  padding: 50px 15px;
}
#aboutUs .aboutUs_rows-brands-holder {
  padding: 0 80px;
}
#aboutUs .aboutUs_rows-brands-holder .aboutUs_rows-brands-item {
  width: 16.66%;
  opacity: 0.6;
  text-align: center;
  transition: transform 0.3s ease;
}
#aboutUs .aboutUs_rows-brands-holder .aboutUs_rows-brands-item:hover {
  opacity: 1;
  transform: scale(1.1, 1.1);
}

#free-shipping {
  background-color: #e8e8e8;
  padding: 80px 0;
}
#free-shipping .free-shipping-box-item {
  width: 50%;
}
#free-shipping .free-shipping-box-item:first-child h3 {
  margin-right: 20px;
}
#free-shipping .free-shipping-box-item:last-child h3 {
  margin-left: 20px;
}
#free-shipping .free-shipping-box-item h3 {
  background-color: #fff;
  padding: 40px 0;
  font-weight: bold;
  color: #000;
  font-size: 1.7rem;
}

/*footer */
#footer .holder {
  padding-top: 70px;
  padding-bottom: 70px;
}
#footer .footer_links {
  width: 25%;
  text-align: left;
  padding: 10px 20px;
}
#footer .footer_links nav h3 {
  margin-bottom: 20px;
}
#footer .footer_links nav ul li {
  padding: 10px 0;
  text-align: left;
}
#footer .footer_links nav ul li a {
  font-weight: bold;
  text-transform: capitalize;
}
#footer .social-media {
  background-color: #e8e8e8;
  padding: 30px 0;
}
#footer .social-media .social-media_item a {
  float: left;
  overflow: hidden;
  padding: 15px 30px;
}
#footer .social-media .copyright {
  padding: 20px;
  width: 100%;
  color: #949494;
}
#footer .social-media .copyright .p {
  padding: 0;
}

/*breakpint size variables*/
@media only screen and (max-width: 1140px) {
  .container {
    width: 100%;
  }
}
@media only screen and (max-width: 992px) {
  header#header {
    display: none;
  }

  header#mobile-header {
    display: block;
  }
}
@media only screen and (max-width: 960px) {
  #aboutUs .news-letter-spacing, #aboutUs .about-delphinus-spacing {
    min-height: 322px;
  }
}
@media only screen and (max-width: 830px) {
  #aboutUs .news-letter, #aboutUs .about-delphinus {
    width: 100%;
  }
  #aboutUs .news-letter {
    margin-top: 1rem;
  }
  #aboutUs .news-letter-spacing {
    min-height: 0;
    padding-bottom: 30px;
  }
}
@media only screen and (max-width: 768px) {
  .gray-background:before {
    height: 145px;
  }

  #chair-section .chairParent .chair {
    display: block;
  }
  #chair-section .chairParent .chair .title {
    font-size: 1.1rem;
  }
  #chair-section .chairParent .chair img {
    max-width: 180px;
  }
  #chair-section .chairParent .chair .stars svg {
    max-width: 17px;
  }

  #rooms .rooms_category {
    width: 100%;
  }
  #rooms .rooms_category img {
    width: 100%;
    height: auto;
  }
  #rooms .rooms_category-name {
    padding: 5px 0;
  }

  #aboutUs .aboutUs_rows-delphinus,
#aboutUs .aboutUs_rows-images {
    text-align: center;
    margin-top: 1rem;
  }
  #aboutUs .aboutUs_rows-images-item {
    width: 100%;
  }
  #aboutUs .aboutUs_rows-images-item-holder {
    margin-top: 0;
    margin-bottom: 15px;
  }
  #aboutUs .aboutUs_rows-brands-holder {
    padding: 0;
  }
  #aboutUs .aboutUs_rows-brands-holder .aboutUs_rows-brands-item {
    width: 33.33%;
  }

  #features .features_products .features_product-item {
    width: 50%;
  }

  #latest {
    margin-top: 0;
  }
  #latest .latest_gallery-item {
    width: 50%;
  }

  #free-shipping .free-shipping-box-item {
    width: 100%;
  }
  #free-shipping .free-shipping-box-item h3 {
    margin: 10px 0 !important;
    font-size: 1.3rem;
  }

  #footer .footer_links {
    width: 50%;
  }
}
@media only screen and (max-width: 578px) {
  #features .features_products .features_product-item {
    width: 100%;
  }

  #aboutUs .aboutUs_rows-brands-holder .aboutUs_rows-brands-item {
    width: 50%;
  }

  #chair-section .chairParent .chair:first-child {
    display: none;
  }
}
@media only screen and (max-width: 491px) {
  #chair-section .chairParent .chair:nth-child(2) {
    display: none;
  }

  #latest .latest_gallery-item {
    width: 100%;
  }
}